import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter); // –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ —Å–≤–æ–π—Å—Ç–≤ VueRouter –≤–æ Vue

import book from './components/book.vue';
import contact from './components/contact.vue';

const routes = [
	{
		path: '/', // –ø–æ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ–º—É –ø—É—Ç–∏ –æ—Ç–∫—Ä—ã—Ç—å –∫–æ–º–ø–æ–Ω–µ–Ω—Ç book
		component: book
	},
	{
		path: '/contact/:contactName', // –ø–æ –ø—É—Ç–∏ /contact/:contactName –æ—Ç–∫—Ä—ã–≤–∞—Ç—å –∫–æ–º–ø–æ–Ω–µ–Ω—Ç contact
		component: contact,
		name: 'toContact',
		props: true // —Ä–∞–∑—Ä–µ—à–∏—Ç—å –ø–µ—Ä–µ–¥–∞–≤–∞—Ç—å –≤—Ö–æ–¥–Ω—ã–µ –ø–∞—Ä–∞–º–µ—Ç—Ä—ã
	}
];

export default new VueRouter({ routes, mode: "history", base: '/contactsBook/'}); // mode: "history" —É–±—Ä–∞—Ç—å # –∏–∑ –∞–¥—Ä–µ—Å–∞