const contacts = {
	state: { // –æ–±—ä—è–≤–ª–µ–Ω–∏–µ –ø–µ—Ä–µ–º–µ–Ω–Ω—ã—Ö
		contacts: [],
		filter: "all"
	},
	mutations: {
		addContact(state, contact) { // –º—É—Ç–∞—Ü–∏—è –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –∫–æ–Ω—Ç–∞–∫—Ç–∞ –≤ –º–∞—Å—Å–∏–≤ contacts
			state.contacts.push(contact);
		},
		removeContact(state, contactId) { // –º—É—Ç–∞—Ü–∏—è —É–¥–∞–ª–µ–Ω–∏—è –∫–æ–Ω—Ç–∞–∫—Ç–∞ –∏–∑ –º–∞—Å—Å–∏–≤–∞ contacts
			state.contacts = state.contacts.filter(contact => contact.id !== contactId);
		},
		checkContact(state, contact) { // –º—É—Ç–∞—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è —Å–æ—Å—Ç–æ—è–Ω–∏—è —Å–≤–æ–π—Å—Ç–≤–∞ checked —É –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–≥–æ –∫–æ–Ω—Ç–∞–∫—Ç–∞
			state.contacts = state.contacts.map(item => (item.id === contact.id ? contact : item));
		},
		filterContacts(state, filter) { // –º—É—Ç–∞—Ü–∏—è –∏–∑–º–µ–Ω–µ–Ω–∏—è —Ç–µ–∫—É—â–µ–≥–æ —Ñ–∏–ª—å—Ç—Ä–∞ –∫–æ–Ω—Ç–∞–∫—Ç–æ–≤
			state.filter = filter;
		},
		addField(state, —Åontact) { // –º—É—Ç–∞—Ü–∏—è –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –ø–æ–ª—è –≤ –ø–µ—Ä–µ–¥–∞–Ω–Ω—ã–π –∫–æ–Ω—Ç–∞–∫—Ç
			state.contacts = state.contacts.map(item => (item.id === —Åontact.id ? —Åontact : item));
		},
		removeField(state, —Åontact) { // –º—É—Ç–∞—Ü–∏—è —É–¥–∞–ª–µ–Ω–∏—è –ø–æ–ª—è –∏–∑ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–≥ –æ–∫–æ–Ω—Ç–∞–∫—Ç–∞
			state.contacts = state.contacts.map(item => (item.id === —Åontact.id ? —Åontact : item));
		}
	},
}

export default contacts;