<template lang="pug">
.book-input
  .input-wrapper
    .error {{ validation.firstError('contact.name') }} <!-- —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ -->
    input.input(
      type="text",
      placeholder="Contact name",
      autofocus,
      v-model="contact.name",
      :class="{ 'valid-error': validation.hasError('contact.name') }",
      @keydown.enter="addNewContact"
    )
  .input-wrapper
    .error {{ validation.firstError('contact.phone') }}
    input.input(
      type="text",
      placeholder="Phone",
      v-model="contact.phone",
      :class="{ 'valid-error': validation.hasError('contact.phone') }",
      @keydown.enter="addNewContact"
    )
</template>

<script>
import { Validator } from "simple-vue-validator"; // –∏–º–ø–æ—Ä—Ç simple-vue-validation –¥–ª—è –ø—Ä–æ–≤–µ—Ä–∫–∏ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ—Å—Ç–∏ –≤–≤–µ–¥–µ–Ω–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö
import { mapMutations } from "vuex"; //–∏–º–ø–æ—Ä—Ç –≤—Å–ø–æ–º–æ–≥–∞—Ç–µ–ª—å–Ω–æ–π —Ñ—É–Ω–∫—Ü–∏–∏ Vuex

let uniqId = 0;
export default {
  mixins: [require("simple-vue-validator").mixin], // –¥–ª—è simple-vue-validation
  data() {
    return {
      contact: {
        id: 0,
        name: "",
        phone: "",
        checked: false,
      },
    };
  },
  validators: { // –≤–∞–ª–∏–¥–∞—Ü–∏—è –ø–æ–ª–µ–π
    "contact.name"(value) {
      // –ø—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –ø—É—Å—Ç–æ—Ç—É, —Ç–æ–ª—å–∫–æ –ª–∞—Ç–∏–Ω—Å–∫–∏–º —Å–∏–º–≤–æ–ª–∞–º
      return Validator.value(value).required("Can't be empty").regex('^[A-Za-z]*$', 'The name can only contain Latin characters');
    },
    "contact.phone"(value) {
      // –ø—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –ø—É—Å—Ç–æ—Ç—É, —Ç–æ–ª—å–∫–æ —Ü–∏—Ñ—Ä–∞–º. –û–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–µ –º–∏–Ω–∏–º–∞–ª—å–Ω–æ–π –∏ –º–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–π –¥–ª–∏–Ω—ã –ø–æ–ª—è
			return Validator.value(value).required("Can't be empty").integer("Check the entered number (unsigned +)").minLength(10, 'Not matched').maxLength(12, 'Must have at least 12 characters');
    },
  },
  methods: {
    ...mapMutations(['addContact']),
    addNewContact() {
      this.$validate().then((seccess) => { // –µ—Å–ª–∏ –≤–∞–ª–∏–¥–∞—Ü–∏—è –ø—Ä–æ—à–ª–∞ —É—Å–ø–µ—à–Ω–æ
        if (!seccess) return;

        uniqId++;
        this.contact.id = uniqId;

        this.addContact({ ...this.contact }); // –≤—ã–∑–æ–≤ –º—É—Ç–∞—Ü–∏–∏

        this.contact.name = "";
        this.contact.phone = "";
        this.validation.reset(); // —Å–±—Ä–æ—Å –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ–± –æ—à–∏–±–∫–∞—Ö, —á—Ç–æ–±—ã —Å–∫—Ä—ã—Ç—å —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.input-wrapper {
  position: relative;

  &::after {
    content: "";
    display: block;
    position: absolute;
    top: 5%;
    right: 0;
    width: 1px;
    height: 90%;
    background-color: rgba(0, 0, 0, 0.03);
  }

  &:last-child {
    &::after {
      display: none;
    }
  }
}

.input {
  font-size: 24px;
  padding: 16px 16px 16px 30px;
  border: 1px solid transparent;
  background: rgba($color: #000000, $alpha: 0.003);
  box-shadow: inset 0 -2px 1px rgba(0, 0, 0, 0.03);
  line-height: 1.4em;
  outline: none;
  color: inherit;
  width: 100%;
}

.valid-error {
  border: 1px solid firebrick;
}

.book-input {
  position: relative;
  display: flex;
}

.error {
  position: absolute;
  bottom: 100%;
  left: 0;
  color: firebrick;
}
</style>