<template lang="pug">
	div
		div.book
			book-input
			book-list(
				v-if="contacts.length > 0" 
				:contacts="filteredContacts"
			) <!-- –µ—Å–ª–∏ –ø–æ–ª–µ –Ω–µ –ø—É—Å—Ç–æ–µ -->
</template>

<script>
import bookInput from "./bookInput"; //–∏–º–ø–æ—Ä—Ç –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ –ø–æ–ª—è –≤–≤–æ–¥–∞
import bookList from "./bookList"; //–∏–º–ø–æ—Ä—Ç –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ —Å–ø–∏—Å–∫–∞ –∫–æ–Ω—Ç–∞–∫—Ç–æ–≤
import { mapState } from "vuex"; //–∏–º–ø–æ—Ä—Ç –≤—Å–ø–æ–º–æ–≥–∞—Ç–µ–ª—å–Ω–æ–π —Ñ—É–Ω–∫—Ü–∏–∏ vuex

export default {
	components: {
		bookInput, bookList
	},
	computed: {
		...mapState({
			contacts: state => state.contacts.contacts, // –∏–º–ø–æ—Ä—Ç –º–∞—Å—Å–∏–≤–∞ –∫–æ–Ω—Ç–∞–∫—Ç–æ–≤ –∏–∑ vuex
			filter: state => state.contacts.filter,
		}),
		filteredContacts() { // –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –ø—Ä–∏ –≤—ã–±—Ä–∞–Ω–Ω–æ–º —Ñ–∏–ª—å—Ç—Ä–µ
			switch(this.filter) {
				case 'all' :
					return this.contacts; // –≤—Å–µ –∫–æ–Ω—Ç–∞–∫—Ç—ã
				case 'important' :
					return this.contacts.filter(item => item.checked === true); // —Ç–æ–ª—å–∫–æ –æ—Ç–º–µ—á–µ–Ω–Ω—ã–µ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º
			}
		}
	},
}
</script>

<style lang="scss" scoped>
	.book {
		margin-top: 100px;
		background: #fff;
		box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .2), 0 25px 50px 0 rgba(0, 0, 0, .1);
	}
</style>