<template lang="pug">
.contact-input
  .input-wrapper
    .error {{ validation.firstError('forChange.title') }}
    input.input(
      type="text",
      placeholder="Title",
      v-model="forChange.title",
      :class="{ 'valid-error': validation.hasError('forChange.title') }",
      @keydown.enter="addNewField"
    )
  .input-wrapper
    .error {{ validation.firstError('forChange.value') }}
    input.input(
      type="text",
      placeholder="Value",
      v-model="forChange.value",
      :class="{ 'valid-error': validation.hasError('forChange.value') }",
      @keydown.enter="addNewField"
    )
</template>

<script>
import { Validator } from "simple-vue-validator"; // –∏–º–ø–æ—Ä—Ç simple-vue-validation –¥–ª—è –ø—Ä–æ–≤–µ—Ä–∫–∏ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ—Å—Ç–∏ –≤–≤–µ–¥–µ–Ω–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö
import { mapMutations, mapState } from 'vuex'
export default {
  mixins: [require("simple-vue-validator").mixin], // –¥–ª—è simple-vue-validation
	props: {
		contactId: Number
	},
  data() {
    return {
      forChange: {
        title: '',
        value: ''
      },
      contactIdObject: { //–∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –æ–±—Ö–µ–∫—Ç–∞ –¥–ª—è –ø–æ–º–æ—â–∏ —Å–≤–æ–π—Å—Ç–≤—É getContact
				contactId: this.contactId,
			}
    }
  },
  validators: { // –≤–∞–ª–∏–¥–∞—Ü–∏—è –ø–æ–ª–µ–π
    "forChange.title"(value) {
      // –ø—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –ø—É—Å—Ç–æ—Ç—É
      return Validator.value(value).required("Can't be empty").maxLength(15, 'Must have at least 15 characters');;
    },
    "forChange.value"(value) {
      // –ø—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ –ø—É—Å—Ç–æ—Ç—É, —Ç–æ–ª—å–∫–æ —Ü–∏—Ñ—Ä–∞–º. –û–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–µ –º–∏–Ω–∏–º–∞–ª—å–Ω–æ–π –∏ –º–∞–∫—Å–∏–º–∞–ª—å–Ω–æ–π –¥–ª–∏–Ω—ã –ø–æ–ª—è
			return Validator.value(value).required("Can't be empty").maxLength(20, 'Must have at least 20 characters');
    },
  },
  computed: {
    ...mapState({
			contacts: state => state.contacts.contacts
    }),
    getContact() { //–∏–∑–≤–ª–µ—á–µ–Ω–∏–µ –∫–æ–Ω—Ç–∞–∫—Ç–∞, –∫–æ—Ç–æ—Ä—ã–π –±—ã–ª –≤—ã–±—Ä–∞–Ω –Ω–∞ –ø–µ—Ä–≤–æ–π —Å—Ç—Ä–∞–Ω–∏—Ü–µ, –∏–∑ store
			return this.contacts.find(function(item) {
				return item.id === this.contactId;
			}, this.contactIdObject);
		}
  },
  methods: {
    ...mapMutations(['addField']),
    addNewField() { // –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ –Ω–æ–≤–æ–≥–æ –ø–æ–ª—è –∫–æ–Ω—Ç–∞–∫—Ç–∞
      this.$validate().then((seccess) => { // –µ—Å–ª–∏ –≤–∞–ª–∏–¥–∞—Ü–∏—è –ø—Ä–æ—à–ª–∞ —É—Å–ø–µ—à–Ω–æ
        if (!seccess) return;
        
        this.getContact[this.forChange.title] = this.forChange.value;
        this.addField(this.getContact);
        this.forChange.title = '';
        this.forChange.value = '';
        this.validation.reset(); // —Å–±—Ä–æ—Å –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏ –æ–± –æ—à–∏–±–∫–∞—Ö, —á—Ç–æ–±—ã —Å–∫—Ä—ã—Ç—å —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ
      });
    },
  }
}
</script>

<style lang="scss" scoped>
.input-wrapper {
  position: relative;

  &::after {
    content: "";
    display: block;
    position: absolute;
    top: 5%;
    right: 0;
    width: 1px;
    height: 90%;
    background-color: rgba(0, 0, 0, 0.03);
  }

  &:last-child {
    &::after {
      display: none;
    }
  }
}

.valid-error {
  border: 1px solid firebrick;
}

.input {
  font-size: 24px;
  padding: 16px 16px 16px 30px;
  border: 1px solid transparent;
  background: rgba($color: #000000, $alpha: 0.003);
  box-shadow: inset 0 -2px 1px rgba(0, 0, 0, 0.03);
  line-height: 1.4em;
  outline: none;
  color: inherit;
  width: 100%;
}

.contact-input {
	position: relative;
  display: flex;
}

.error {
  position: absolute;
  bottom: 100%;
  left: 0;
  color: firebrick;
}

</style>