<template lang="pug">
	.contact-info
		ul.list
			li(v-for="key in contactKeysArray").item
				contact-info-list-item(
					:field="key"
					:contact="getContact"
				)
</template>

<script>
import contactInfoListItem from "./contactInfoListItem";
import { mapState } from "vuex";
export default {
	props: {
		contactId: Number
	},
	data() {
		return {
			contactIdObject: { //–∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –æ–±—Ö–µ–∫—Ç–∞ –¥–ª—è –ø–æ–º–æ—â–∏ —Å–≤–æ–π—Å—Ç–≤—É getContact
				contactId: this.contactId,
			}
		}
	},
	components: {
		contactInfoListItem
	},
	computed: {
		...mapState({
			contacts: state => state.contacts.contacts
		}),
		contactKeysArray() { // —É–¥–∞–ª–µ–Ω–∏–µ —Å–≤–æ–π—Å—Ç–≤ id –∏ phone –∏–∑ –æ–±—ä–µ–∫—Ç–∞ –¥–ª—è –ø–µ—Ä–µ–¥–∞—á–∏ –æ–±—ä–µ–∫—Ç–∞ –≤ —Ü–∏–∫–ª –∏ –≤—ã–≤–æ–¥–∞ –µ–≥–æ —Å–≤–æ–π—Å—Ç –Ω–∞ —ç–∫—Ä–∞–Ω
			let contactKeyArray = Object.keys(this.getContact);
			contactKeyArray.splice([contactKeyArray.indexOf("id")], 1);
			contactKeyArray.splice([contactKeyArray.indexOf("checked")], 1);
			return contactKeyArray;
		},
		getContact() { //–∏–∑–≤–ª–µ—á–µ–Ω–∏–µ –∫–æ–Ω—Ç–∞–∫—Ç–∞, –∫–æ—Ç–æ—Ä—ã–π –±—ã–ª –≤—ã–±—Ä–∞–Ω –Ω–∞ –ø–µ—Ä–≤–æ–π —Å—Ç—Ä–∞–Ω–∏—Ü–µ, –∏–∑ store
			return this.contacts.find(function(item) {
				return item.id === this.contactId;
			}, this.contactIdObject);
		}
	}
}
</script>